/**
 * Withdrawals Section - Plugin wrapper
 *
 * Admin withdrawal pipeline: privacy period, queue, and history.
 */

import React, { useEffect, useState } from 'react';
import type { AdminSectionProps } from '../types';
import {
  AdminPrivacyPeriodDeposits,
  AdminWithdrawalQueue,
  AdminWithdrawalHistory,
  AdminWithdrawalStats,
} from '../../components/deposit/admin';

export default function WithdrawalsSection({
  pageSize = 20,
  refreshInterval = 0,
}: AdminSectionProps): React.JSX.Element {
  const [refreshSignal, setRefreshSignal] = useState(0);

  useEffect(() => {
    if (refreshInterval <= 0) return;
    const interval = window.setInterval(() => {
      setRefreshSignal((prev) => prev + 1);
    }, refreshInterval);
    return () => window.clearInterval(interval);
  }, [refreshInterval]);

  return (
    <div className="cedros-dashboard__withdrawals">
      <AdminWithdrawalStats refreshSignal={refreshSignal} />
      <p className="cedros-dashboard__text-muted">
        Track deposits through the withdrawal pipeline: privacy period, ready for processing, and
        completed.
      </p>

      {/* Pipeline view: Privacy Period -> Ready -> Completed */}
      <div className="cedros-dashboard__pipeline">
        <AdminPrivacyPeriodDeposits pageSize={pageSize} refreshSignal={refreshSignal} />
        <AdminWithdrawalQueue pageSize={pageSize} refreshSignal={refreshSignal} />
        <AdminWithdrawalHistory pageSize={pageSize} refreshSignal={refreshSignal} />
      </div>
    </div>
  );
}
